<template>
     <el-form :model="searchForm" ref="searchForm"  label-width="80px" >
            <el-row>
                <el-col :span="6">
                  <el-form-item label="仓库" prop="warehouseUuid">
                    <el-select filterable placeholder="请选择仓库" v-model="searchForm.warehouseUuid"   @change="getMerchantsList">
                        <el-option
                          v-for="(item, index) in warehouseList"
                          :key="index"
                          :label="item.warehouseName"
                          :value="item.warehouseUuid"
                        >{{item.warehouseName}}</el-option>
                    </el-select>
                  </el-form-item>
                </el-col>
                <el-col :span="6">
                    <el-form-item label="收货单号"  prop="orderId">
                      <el-input placeholder="请输入收货单号" clearable v-model="searchForm.orderId"   ></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="6">
                    <el-form-item label="商户"  prop="merchantUuid">
                      <el-select clearable filterable placeholder="请选择商户" v-model="searchForm.merchantUuid" >
                        <el-option
                          v-for="(item, index) in merchantsList"
                          :key="index"
                          :label="item.userName"
                          :value="item.userUuid"
                        >{{item.userName}}</el-option>
                      </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="6">
                    <el-form-item label="状态" prop="status">
                      <el-select placeholder="请选择状态"  filterable clearable v-model="searchForm.status" >
                        <el-option label="待修复" value="0"/>
                        <el-option label="已修复" value="1"/>
                        <el-option label="修复未通过" value="2"/>
                      </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="6">
                    <el-form-item label="开始日期"  prop="beginDate">
                      <el-date-picker
                        class="w100p"
                        v-model="searchForm.beginDate"
                        type="date"
                        align="right"
                        clearable
                        unlink-panels
                        range-separator="至"
                        placeholder="请选择开始日期"
                        format="yyyy 年 MM 月 dd 日"
                        value-format="yyyy-MM-dd"
                        size="mini"
                        >
                      </el-date-picker>
                    </el-form-item>
                </el-col>
                <el-col :span="6">
                    <el-form-item label="结束日期"  prop="endDate">
                      <el-date-picker
                        class="w100p"
                        v-model="searchForm.endDate"
                        type="date"
                        align="right"
                        clearable
                        unlink-panels
                        range-separator="至"
                        placeholder="请选择结束日期"
                        format="yyyy 年 MM 月 dd 日"
                        value-format="yyyy-MM-dd"
                        size="mini"
                        >
                      </el-date-picker>
                    </el-form-item>
                </el-col>
                <el-col :span="6" class="flex-right">
                    <el-button type="primary" @click.native="search">查询</el-button>
                    <el-button @click="resetForm('searchForm')">重置</el-button>
                </el-col>
            </el-row>
          </el-form>
</template>

<script>
/**
 * Maintenance Management Module
 * ----------------------
 * Author: Calamus
 * Date: 2018.08.29
 */
import { setSession, getSession } from '@/utils/session'
import moment from 'moment'


export default {
  name: 'MainManagementSearch',
  data () {
    return {
      // 检索界面数据
      searchForm: {
        storageUuid: '',
        warehouseUuid: '',
        merchantUuid: '',
        timeRange:[],
        status:'',
        inventoryUuid:'',
        productTempleteUuid:'',
        testFrom:'',
        beginDate:'',
        endDate:'',
        orderId:'',
      },

    }
  },
  mounted () {
      this.searchForm.warehouseUuid = getSession('warehouseUuid')
      this.search();
      this.getMerchantsList(this.searchForm.warehouseUuid)
  },
  props: {
    // statusList:[]
    warehouseList:{
      type: Array,
      required: true
    },
    merchantsList:{
      type: Array,
      required: true
    }
  },
  methods: {
    getMerchantsList (value) {
        setSession('warehouseUuid',value)
        this.searchForm.merchantUuid = ''
        this.$emit('getMerchants',Object.assign({},this.searchForm))

      },
    // 检索
    search: function () {
      if (this.searchForm.endDate && this.searchForm.beginDate &&  ( moment(this.searchForm.endDate).isBefore(this.searchForm.beginDate) || moment(this.searchForm.endDate).isSame(this.searchForm.beginDate))) {
              this.$message({
                message: '结束日期不能早于开始日期，请重新选择！',
                type: 'warning'
              })
              this.searchForm.endDate = null ;
              return
      }else{
        this.$emit('search', Object.assign({}, this.searchForm))
      }
    },
    // 重置
    resetForm (formName) {
      this.$refs[formName].resetFields()
      this.searchForm.warehouseUuid = getSession('warehouseUuid')
      this.$emit('rest', Object.assign({}, this.searchForm))
    }
  }

}
</script>
<style lang="scss" scoped>
.el-form-item{
   margin-bottom:10px;
}
</style>



