<template>
    <div>
      <!-- 查询 -->
      <common-wrapper>
        <main-management-search
          class="search"
          :warehouseList="warehouseList"
          :merchantsList="merchantsList"
          @search="queryList"
          @getMerchants="getMerchantsList"
          @rest="rest" />
      </common-wrapper>
      <!-- <header-breadcrumb :breadData="$route.matched" /> -->
      <nav-button>
        <el-button-group>
           <el-button icon="fdddfont icon-shouquan"   :disabled="isButtonIsDisabled()" @click="handleMaintained(1)">已修复</el-button>
          <el-button icon="fdddfont icon-add-new"   :disabled="isButtonIsDisabled()"  @click="handleMaintained(2)">未修复</el-button>
        </el-button-group>
        <span v-if="StatusTrueflag" style="color:red">已修复的数据不能再操作，请取消勾选！</span>
      </nav-button>

     <!-- 用户列表 -->
      <common-wrapper>
        <common-table :flex="270">
          <template slot="table">
            <el-table
              ref="userTable"
              height="100%"
              stripe
              :border="true"
              v-loading="isListLoading"
              :data="tableData"
              @row-click="getUserDetail"
              @selection-change="handleSelectionChange">
                <el-table-column type="selection" width="30" ></el-table-column>
                <!--<el-table-column type="index" :index="indexMethod" label="序号" width="50"></el-table-column>-->
                <el-table-column sortable prop="orderUuid" label="收货单号" width="150"></el-table-column>
                <el-table-column sortable prop="merchantName" label="商户" min-width="120"></el-table-column>
                <el-table-column sortable prop="productBrand" label="品牌" ></el-table-column>
                <el-table-column sortable prop="productName" label="货品名称" ></el-table-column>
                <el-table-column sortable prop="productModel" label="型号" ></el-table-column>
                <el-table-column sortable prop="productColor" label="颜色" ></el-table-column>
                <el-table-column sortable prop="productPackFlag" label="货品类型" :formatter="typeFormatter"></el-table-column>
                <el-table-column prop="goodRepairResult" label="维修方式" width="120" :formatter="formatResult"></el-table-column>
                <el-table-column prop="goodRepairStatus" label="状态" width="80" :formatter="formatState"></el-table-column>
                <el-table-column sortable prop="warehousName" label="仓库" width="120"></el-table-column>
               <el-table-column prop="goodTestRemark" label="质检不通过的原因" min-width="120" :show-tooltip-when-overflow="true"></el-table-column>
            </el-table>
          </template>
          <template slot="pagination">
                <el-pagination   background   layout="total, prev, pager, next"
                :current-page.sync="pagination.pageNum"
                :page-size="pagination.pageSize"
                :total="pagination.total"
                @current-change="changePage"
                ></el-pagination>
          </template>
        </common-table>
      </common-wrapper>

     <!-- 已修复 -->
     <!-- 未修复 -->
<!-- 质检不通过 -->
     <status-change
        :show.sync="isChangeVisible"
        :modifyUser.sync="selectRow"
        :searchParams="searchParams"
        @updateList="queryList"
        :flag="Maintext"
      />
    </div>
</template>

<script>
/**
 * Maintenance Management Module
 * ----------------------
 * Author: Calamus
 * Date: 2018.08.29
 */
import moment from 'moment'
// 全局混入
import {mixins} from '@/mixins'
// 自定义组件
import NavButton from '@/components/navButton'
import NavPremissonButton from '@/components/navButton/navPremissonButton'
import CommonWrapper from '@/components/commonWrapper'
import CommonTable from '@/components/commonTable'
import MainManagementSearch from './Components/Search'
import statusChange from './Components/maintenance'

// API
import { getUsersList, deleteUser } from '@/api/user'
import { getAllOrgList,getAllMerchantsList } from '@/api/common'
import { maintenanceList, maintenanceUpdate, getWarehouseList,maintenanceStatusList,maintenanceMethodsList } from '@/api/warehouse'
import { setSession, getSession } from '@/utils/session'


export default {
  name: 'MaintenanceManagement',
  mixins: [mixins],
  components: {
    NavButton,
    NavPremissonButton,
    CommonWrapper,
    CommonTable,
    MainManagementSearch,
    statusChange
  },
  data () {
    return {
      comp: this,
      buttonData: [],
      tableData: [],
      selectRow: [], // 列表选中列
      choosedIds:[],
      merchantsList:[],
      isListLoading: false, // 列表加载
      StatusTrueflag:false,//选项中是否有已修复和未数据
      warehouseList:this.$store.getters.warehouseList,
      isCreateFormVisible: false, // 新增界面是否显示
      isEditFormVisible: false, // 编辑界面是否显示
      isRoleAuthVisible: false, // 用户角色授权窗口是否展示
      modifyUser: null, // 弹出框编辑时存放的临时行数据
      isChangeVisible:false,
      Maintext:'',
      pagination: { // 翻页数据
        pageNum: 1,
        pageSize: 10,
        total: 0
      },
      searchParams: {} // 查询参数
    }
  },
  activated () {
    this.$refs.userTable.doLayout()
  },
  mounted () {
  },
  methods: {
    // 查询和初始化查询
    // 查询/翻页公用方法
    queryList (params) {
      const searchParams = this.searchParams = params
      this.getUsers(
        {
          ...searchParams,
          pageNum: this.pagination.pageNum,
          pageSize: this.pagination.pageSize
        }
      )
    },
    //获取仓库列表
    getWarehouse(){
      getWarehouseList().then(res => {
        console.info("warehouse=>",res)
              this.warehouseList = res;
      })
    },
    getMerchantsList (params) {
        return new Promise(resolve => {
          getAllMerchantsList(params).then(res => {
            if(!res.error) {
              this.merchantsList = res
              resolve()
            }
          })
        })
      },
    // 获取列表
    getUsers (params) {
      this.isListLoading = true
      maintenanceList(params).then(res => {
        if(res.item.length == 0 && res.pageNum > 1){
          this.pagination.pageNum = 1
          params.pageNum = 1
          this.getUsers(params)
          return
        }
        this.tableData = res.item
        this.pagination.total = res.total;
        this.pagination.pageNum = res.pageNum
        this.pagination.pageSize = res.pageSize
        this.isListLoading = false
      })
    },
    rest (params) {
      const searchParams = this.searchParams = params
      this.getUsers(
        {
          ...searchParams,
          pageNum: 1,
          pageSize: this.pagination.pageSize
        }
      )
    },
    handleSelectionChange(item){
      this.selectRow = item ;
      let oIds = [] ;
      this.StatusTrueflag = false ;
      this.choosedIds = this.selectRow.map(row => {
        oIds.push(row.goodRepairUuid);
        if(row.goodRepairStatus == 1){
          this.StatusTrueflag = true ;
        }
        });
    },
    isButtonIsDisabled () {
      if( this.selectRow.length > 0 && !this.StatusTrueflag){
        return false ;
      }
      return true
    },
    handleMaintained(flag){
      //flag为1表示已修复，2为未修复
      this.Maintext = flag == 1 ? '已修复' : '未修复' ;
      if (!this.selectRow.length) {
                    this.$message({
                      type: 'warning',
                      message: '请至少选择一项',
                    })
            }else if(this.StatusTrueflag){
                this.$message({
                      message: '所选择项中有已修复的数据，请重新选择！',
                      type: 'warning'
                    })
            }else{
              this.isChangeVisible = true ;
            }
    },
    isButtonDisabled () {
      return !!this.selectRow.length
    },
    formatState (row,col) {
      switch(row.goodRepairStatus){
        case '0':
          return '待修复';
          break;
        case '1':
          return '已修复';
          break;
        case '2':
          return '修复未通过';
          break;
      }
    },
    formatResult(row,col){
      switch(row.goodRepairResult){
        case '0':
          return '继续收货';
          break;
        case '1':
          return '返厂';
          break;
        case '2':
          return '第三方维修';
          break;
        case '3':
          return '星和宅配维修';
          break;
      }
    },
    typeFormatter (row) {
      switch(row.productPackFlag){
        case '0':
          return '小件'
          break;
        case '1':
          return '套件'
          break;
        case '2':
          return '单品'
          break;
        default:
          return '未知'
      }
    },
    // 查询详情
    getUserDetail (row, event) {
      // console.log(row)
    },
    // 单行删除
    handleDel (index, row) {
      this.$confirm('确认删除该吗?', '提示', {
        type: 'warning'
      }).then(() => {
        // this.deleteUsers([row.userUuid])
      }).catch(() => {})
    },
    // 批量删除
    batchRemove () {
      this.$confirm('确认删除选中吗？', '提示', {
        type: 'warning'
      }).then(() => {
        let userIds = []
        this.selectRow.map(row => userIds.push(row.userUuid))
        this.isListLoading = true
        // this.deleteUsers(userIds).then(() => {
        //   this.isListLoading = false
        // })
      }).catch(() => {})
    },
    // 删除接口方法
    deleteUsers (userIds) {
      return deleteUser(userIds).then((res) => {
        this.$message({
          message: '删除成功',
          type: 'success'
        })
        this.queryList()
      })
    },
    // 列表翻页
    changePage (pageNum) {
      this.pagination.pageNum = pageNum
      this.queryList(this.searchParams)
    },
    // 生成列表序号
    indexMethod (index) {
      return (index + 1) + (this.pagination.pageNum - 1) * this.pagination.pageSize
    },
    // 列表状态显示格式化
    formatStatus (row, column) {
      return row.userEnable === '1' ? '启用' : row.userEnable === '0' ? '禁用' : '未知'
    },
    // 列表日期格式化
    formatCreateDate (row) {
      return moment(row.userCreateTime).utcOffset(8).format('YYYY-MM-DD')
    }
  }
}
</script>



